*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.search-images {
  padding-top: 30px;
  padding-bottom: 30px;
}

.search-form {
  width: 570px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 24px;
}

input[placeholder='Search images...'] {
  width: 100%;
  padding: 16px;
  border: 1px solid;
  border-radius: 4px;

  font-family: Arial, Helvetica, sans-serif;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: 0.03em;
  background-color: transparent;
  outline: none;
}

.gallery {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  max-width: calc(100% - 64px);
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-gap: 24px;
}

.photo-card {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.photo-card img {
  display: block;
  height: 300px;
  object-fit: cover;
  width: 100%;

  cursor: pointer;
}

.photo-card:hover .stats {
  transform: translateY(0);
}

.stats {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  padding: 8px;
  flex-grow: 1;
  transform: translateY(100%);

  background-color: rgba(47, 48, 58, 0.2);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.stats-item {
  display: flex;
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;

  color: #ffffff;
  font-size: 14px;
}

.stats-item i {
  margin-right: 8px;

  font-size: 22px;
}
